@import '../../style/variables.scss';
.Interested{
    padding: $gutter;
    background: $background;
    text-align: center;
}

@media(min-width: 750px){
    .Interested{
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 60px $gutter;
    }
    h2{
        width: 400px;
    }
    .line{
        height: 1px;
        background: $dark;
        width: 250px;
        opacity: .3;
        margin: 0 10px;
    }
}

@media(min-width: 1100px){
    .Interested{
        padding: 80px $gutter2;
       
    }

}