@import '../../../../style/variables.scss';
.GetInTouch{
    margin: 20px;
    padding-bottom: 40px;
    border-top: $borderLine;
    border-bottom: $borderLine;
}

.socialIcons{
    padding: $gutter 0;
    svg{
        margin-right: 10px;
    }
}


@media(min-width: 1000px){
    .GetInTouch{
        display: flex;
        justify-content: space-between;
        padding: 0 $gutter2;
        h1{
            flex: 1;
            
        }
        .GetInTouchDesc{
           flex:1;
           padding-top: $gutter;
        }
    } 
}