@import '../../../../style/variables.scss';



.hero{
    padding: $gutter;
    background: $background;
}

.img{
    width: 100%;
    height: 50vh;
    background: center / cover no-repeat url("../../../../images/homepage/mobile/image-homepage-hero@2x.jpg");
}
 

@media(min-width: 750px){
    .hero{
        height: 90vh;
    }
    .img{
        height: 80vh;
        background: center / cover no-repeat url("../../../../images/homepage/tablet/image-homepage-hero@2x.jpg");
      
    }
    .heroBlock{
       transform: translateY(-100%) ;
       background: $background;
       width: 80%;
       h1{
           padding-right: 40px;
       }
    }

}
@media(min-width: 1100px){
    .hero{
        padding: $gutter $gutter2;
    }
    .img{
        background: center / cover no-repeat url("../../../../images/homepage/desktop/image-homepage-hero@2x.jpg");
      
    }
    .heroBlock{
        width: 50%;
    }
    
}