@import '../../style/variables.scss';

.header{
    height: 80px;
    background: $background;   
    display: flex;
    justify-content: space-between;
    align-items: center; 
    padding: 0 $gutter;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 100;
}

.hamburger{
    position: relative;
    cursor: pointer;
}

.navigation{
    display: none;
}

@media(min-width: 750px){
    .hamburger{
        display: none;
    }
    .navigation{
        display: initial;
        display: flex;

        li{
            margin-left: 13px;
            text-transform: uppercase;
            font-size: 14px;
            letter-spacing: 1px;
            cursor: pointer;
            &:hover{
                color: $primary;
            }
        }
    }
}

@media(min-width: 1100px){
    .header{
        padding: 0 $gutter2;
       
    }
    
}