@import '../../../style/variables.scss';

.Project{
padding: 0 $gutter;
}
.projectTopImg{
    height: 30vh;
}
.projectImg{
    height: 400px;
}
.topText{
    border-top:$borderLine;
    border-bottom:$borderLine;
    margin: $gutter 0;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(4, min-content);
    button{
        width: 170px;
    }
}
.mb2{
    margin-bottom: 20px;
}
.projectPreview{
    height: 30vh;
    margin: $gutter 0;
}

.switchProject{
    display: flex;
    justify-content: space-between;
    padding: 10px;
    .left,.right{
        border-top:$borderLine;
        border-bottom:$borderLine;
        flex: 1;
        padding: 15px;
        cursor: pointer;
    }
    .left{
        border-right:$borderLine;
       
    }
    .right{
        text-align: right;
    
    }
}

.gridP{
    padding-bottom: $gutter;
}

.buttonRow{
    display: flex;
    
}

// project one ///////////

.projectOneImg{
    background: center/cover no-repeat url('../../../images/detail/mobile/image-manage-hero@2x.jpg');

}
.projectOnePreviewOne{

    background: center/cover no-repeat url('../../../images/detail/mobile/image-manage-preview-1@2x.jpg');
  
}
.projectOnePreviewTwo{
    background: center/cover no-repeat url('../../../images/detail/mobile/image-manage-preview-2@2x.jpg');
}


