@media(min-width: 700px){
    .topText{
        grid-template-columns: 1fr 1fr;
        grid-template-rows: repeat(3, min-content);
    }
    .gridH1{
        grid-column: 1/2;

      
        
    }
    .gridP{
        grid-column: 2/3;
        grid-row: 1/-1;
        align-self: center;
    
        
    }
    .gridH6{
        padding-bottom: 20px;
        grid-column: 1/2;
       
 
    }
    .topText{
        button{
            grid-column: 1/2;
            margin-top: 0;
        
        }
