@import '../../../../style/variables.scss';
.Project{
   padding: 0 $gutter;
}

.ProjectDescription{
    border-top: $borderLine;
    border-bottom: $borderLine;
}
.ProjectImg{
    min-height: 40vh;
    margin: 40px
   
}
.ProjectOneImg{
background: center/cover no-repeat url('../../../../images/portfolio/mobile/image-portfolio-manage@2x.jpg')
}
.ProjectTwoImg{

background: center/cover no-repeat url('../../../../images/detail/mobile/restraunt-mobile.png');
background-size: cover;
}
.ProjectThreeImg{
background: center/cover no-repeat url('../../../../images/detail/desktop/mobile-countries.png')
}

@media(min-width: 800px){
  .Project{
      display: flex;
      padding: 40px;
  }
  .ProjectImg{
   flex:1;
  
   
}
.ProjectDescription{
    flex:1;
}
.ProjectOneImg{
    background: center/cover no-repeat url('../../../../images/portfolio/tablet/image-portfolio-manage@2x.jpg')
    }
    .ProjectTwoImg{
    order: 2;
   
    }
   
}

@media(min-width: 1100px){
    .ProjectImg{
      margin-top:0;
      margin-bottom: 0;
    }
   
 
 
  .ProjectOneImg{
      background: center/cover no-repeat url('../../../../images/portfolio/desktop/image-portfolio-manage@2x.jpg')
      }
     
  .ProjectTwoImg{
        background: center/cover no-repeat url('../../../../images/detail/desktop/restaurant-1.png');
      }
     
   
  }
