@import '../../../style/variables.scss';
.secondaryButton{
    
    height: 50px; 
    min-width: 40px;
    padding: 0 20px;
    background: $textLight;
    color: $dark;
    border: 1px solid $dark;
    cursor: pointer;
    text-transform: uppercase;
    letter-spacing: 2px;
    margin: 35px 0;
    margin-right: 10px;
    &:hover{
        background: $dark;
        color: $textLight;
    }
}