@import '../../../../style/variables.scss';
.About{
    background: $background;
    padding: 0 $gutter;
}

.aboutText{
    border-top: $borderLine;
    border-bottom: $borderLine;
    margin-bottom: 0;
}

.img{
    background: center / cover no-repeat url("../../../../images/portfolioPicture.jpg");
    height: 60vh;
    background-size: cover;
}



@media(min-width: 700px){
    .About{
        display: flex;
    }
    .img{
        background: center / cover no-repeat url("../../../../images/portfolioPicture.jpg");
        height: 80vh;
       flex: 2;
    }
    .aboutText{
        margin: 0;
        flex: 3;
        margin-left: 20px; 
        padding: 0 40px;
    } 
}


@media(min-width: 1100px){
    .About{
        padding: 0 $gutter2;
    }
 
    .img{
        background: center / cover no-repeat url("../../../../images/portfolioPicture.jpg");
        height: 80vh;
       flex: 1;
    }
    .aboutText{
        margin: 0;
        flex: 1;
        margin-left: 20px; 
    } 
}