@import '../../style/variables.scss';
.Footer{
    background: $dark;
    color: $textLight;
    text-align: center;
    padding: 40px 0;

    li{
        text-transform: uppercase;
        letter-spacing: 2px;
        font-size: 14px;
        padding: 10px 0;
        &:hover{
            color: $primary;
        }
    }

    svg{
        margin: 0 8px;
    }
    .logo,.socialIcons{
        margin: 15px 0;
    }
}

@media(min-width: 750px){
       .Footer{
        padding: $gutter $gutter2;
        display: flex;
        justify-content: space-between;
        align-items: center;
        .logo{
            margin-right: 20px;
        }
       
        }
        .FooterNav{
            display: flex;
            align-items: center;
        }
        ul{
            display: flex;
           
            li{
                margin: 0 10px;
            }
        }
}