@import '../../../style/variables.scss';
.dropdown{
    background: $dark;
    position: absolute;
    top: 54px;
    right: 0;
    color: white;
    padding: $gutter;
    width: 250px;    
    text-align: center;
    li{
        text-transform: uppercase;
        letter-spacing: 2px;
        font-size: 14px;
        padding: 10px 0;
        &:hover{
            color: $primary
        }
    }

    li:not(:last-child){
        padding-bottom: $gutter;
   
    }
}