@import url('https://fonts.googleapis.com/css2?family=Ibarra+Real+Nova:wght@400;700&family=Public+Sans&display=swap');
@import './variables.scss';
body{
    font-size: 1.8rem;
    font-family: 'Public Sans';
    color: $dark;
    line-height: 1.8;
}

h1{
    font-weight: 700;
    font-family: 'Ibarra Real Nova';
    line-height: 1.2;
}
h1{
    font-size: 3.7rem;
    padding: $gutter 0;
    margin: 0;
    @media(min-width: 750px){
        font-size: 5rem;
    }
}
h2{
    font-size: 40px;
    font-weight: normal;
    font-family: 'Ibarra Real Nova';
    line-height: 1.2;
}

h3{
    font-size: 32px;
    font-weight: regular;
    font-family: 'Ibarra Real Nova';
    margin: 0;
    padding:0;
    
}
p{
    opacity: .7;
    font-size: 1.8rem;
    margin: 0;
    padding: 0;
}
h6{
    margin: 0;
    color: $primary;
    font-size: 1.6rem;
    line-height: 1.2;
}