@import '../../../style/mixins.scss';
@import '../../../style/variables.scss';

.primaryButton{
    background: $secondary;
    color: $textLight;
    display: flex;
    padding: 0px;
    align-items: center;
    cursor: pointer;
    border:none;
    .buttonText{
        padding: 0 30px;
        text-transform: uppercase;
        letter-spacing: 2px;
    }
    .icon{
        background: darken($secondary, 8%);
        padding: 15px;
    }
  
    &:hover{
        background: $primary;
        .icon{
            background: darken($primary, 8%);
        }
    }
}