@import '../../../../style//variables.scss';
.Contact{
    padding: 0 $gutter;
    padding-bottom: $gutter;
} 

.error{
    color: $error;
    font-size: 12px;
   font-style: italic;
    padding-left: 10px;

}

.ContactForm{
    label{
        font-size: 1.4rem;
       
    }
    input, textarea{
        display: block;
        width: 100%;
        background: $textField;
        border: none;
        padding: 15px;
        margin: 5px 0;
    }
  button{
      margin: $gutter 0;
  }
}


@media(min-width: 1000px){
    .Contact{
        display: flex;
        justify-content: space-between;
        padding: 0 $gutter2;
        h1{
            flex: 1;
        }
        .ContactForm{
            flex: 1;
        }
        
    } 
}